"use client";

import useAuthStore from "@/store/MyAuthState";
import { PlusOutlined } from "@ant-design/icons";
import { Button, Drawer, Form, Input, Select } from "antd";
import FormItem from "antd/es/form/FormItem/index.js";
import axios from "axios";
import { useState } from "react";

function AddStudent({ onClose, open, showDrawer }: any) {
  const MyAuthState = useAuthStore();
  const [loading, setLoading] = useState(false);

  return (
    <>
      <Button type="primary" onClick={showDrawer} icon={<PlusOutlined />}>
        Student qo'shish
      </Button>
      <Drawer
        title="Yangi student qo'shish"
        width={500}
        onClose={onClose}
        open={open}
        destroyOnClose
      >
        <Form
          onFinish={async (values) => {
            setLoading(true);
            try {
              const res = await axios.post(
                `https://nt.softly.uz/api/app/users`,
                {
                  name: values.name,
                  email: values.email,
                  role: values.role,
                  password: values.password,
                  image: values.image || null,
                },
                {
                  headers: {
                    Authorization: `Bearer ${MyAuthState.token}`,
                  },
                }
              );

              onClose(false);
            } catch (error) {
              console.error("Xatolik:", error);
            } finally {
              setLoading(false);
            }
          }}
        >
          <FormItem
            label="Ism"
            name="name"
            rules={[{ required: true, message: "Ism kiritilmadi!!!" }]}
          >
            <Input />
          </FormItem>
          <FormItem
            label="Email"
            name="email"
            rules={[{ required: true, message: "Email kiritilmadi!!!" }]}
          >
            <Input />
          </FormItem>
          <FormItem
            label="Roli"
            name="role"
            rules={[{ required: true, message: "Roli kiritilmadi!!!" }]}
          >
            <Select
              options={[
                { label: "Customer", value: "customer" },
                { label: "Admin", value: "admin" },
              ]}
            />
          </FormItem>
          <FormItem
            label="Parol"
            name="password"
            rules={[
              {
                required: true,
                min: 6,
                message: "Parol kamida 6 ta belgidan iborat bo'lishi kerak!!!",
              },
            ]}
          >
            <Input.Password />
          </FormItem>
          <FormItem
            label="Rasm URL"
            name="image"
            rules={[{ required: false, message: "Rasm URL kiritilmadi!!!" }]}
          >
            <Input placeholder="https://example.com/image.jpg" />
          </FormItem>
          <FormItem>
            <Button type="primary" htmlType="submit" loading={loading}>
              Submit
            </Button>
          </FormItem>
        </Form>
      </Drawer>
    </>
  );
}

export default AddStudent;
