"use client";
import AddStudent from "@/app/students/edits/AddStudent";
import DeleteStudents from "@/app/students/edits/DeleteStudents";
import EditStudents from "@/app/students/edits/EditStudents";
import useGlobalStore from "@/store/my-store";
import useAuthStore from "@/store/MyAuthState";
import { useFetchData } from "@/utils/axiosData/getData";
import { Button, Switch, Table } from "antd";
import axios from "axios";
import { useEffect, useState } from "react";

export type UserType = {
  id: number;
  name: string;
  email: string;
  password: string;
  image: string;
  role: string;
};

function SrudentsPage() {
  const MyAuthState = useAuthStore();
  const [AddOpen, setAddOpen] = useState(false);
  const [editOpen, setEditOpen] = useState(false);
  const [EditStudent, setEditStudent] = useState({});
  const [deleteStudent, setDeleteStudent] = useState({});
  const { data: user } = useFetchData<UserType>(`/app/users`);

  const showDrawer = () => {
    setAddOpen(true);
  };
  const showDrawerEdit = () => {
    setEditOpen(true);
  };

  const onClose = () => {
    setAddOpen(false);
  };
  const onCloseEdit = () => {
    setEditOpen(false);
  };

  return (
    <div className="flex flex-col items-center">
      <div className="flex gap-2 items-center justify-center  ">
        <Button
          onClick={() => {
            console.log(`Userlarning umumiy soni ${user.length}`);
          }}
        >
          Umumiy son: {user.length}
        </Button>
      </div>

      <div className="flex flex-col my-5">
        <AddStudent onClose={onClose} open={AddOpen} showDrawer={showDrawer} />
        {/* <EditStudents
          editOpen={editOpen}
          showDrawerEdit={showDrawerEdit}
          onCloseEdit={onCloseEdit}
          EditStudent={EditStudent}
          setEditStudent={setEditStudent}
        /> */}
        <Table
          columns={[
            {
              title: "ID",
              dataIndex: "id",
              render: (id: any, student, s) => {
                return (
                  <span
                    className=" cursor-pointer "
                    onClick={() => {
                      setEditOpen(true);
                      setEditStudent(student);
                    }}
                  >
                    {id}
                  </span>
                );
              },
            },
            {
              title: "Email",
              dataIndex: "email",
            },
            {
              title: "Photo",
              dataIndex: "image",
              render: (img, user) => {
                return img ? (
                  <img src={img} alt={user.name} />
                ) : (
                  <span>Mavjud emas</span>
                );
              },
            },
            {
              title: "Roli",
              dataIndex: "role",
            },
            {
              title: "Ismi",
              dataIndex: "name",
            },
            {
              title: "Saqlangan",
              dataIndex: "createdAt",
              render: (create) => {
                const date = new Date(create);
                const options: any = {
                  year: "numeric",
                  month: "long",
                  day: "numeric",
                  hour: "2-digit",
                  minute: "2-digit",
                  second: "2-digit",
                  hour12: false,
                };
                return <span>{date.toLocaleString(undefined, options)}</span>;
              },
            },
          ]}
          dataSource={user.map((i: any) => ({ ...i, key: i.id }))}
        />
        <Button
          color="danger"
          type="text"
          variant="text"
          onClick={() => {
            console.log("Hozircha hammasini o'chirish ishlamaydi !!!");
          }}
        >
          Hammasini ochirish{" "}
        </Button>
      </div>
    </div>
  );
}

export default SrudentsPage;
